@media (min-width: 0em) {
    main {
        margin-left: 10%;
        margin-right: 10%;
    }

    #gluttony {
        max-width: 90%;
        max-height: 90%;
    }
}

@media (min-width: 1200px) {
    main {
        margin-left: 20%;
        margin-right: 20%;
    }

    #gluttony {
        max-width: 50%;
        max-height: 50%;
    }
}

@media (min-width: 1500px) {
    main {
        margin-left: 25%;
        margin-right: 25%;
    }

    #gluttony {
        max-width: 50%;
        max-height: 50%;
    }
}

body {
    margin: 0;
    background-color: whitesmoke;
    line-height: 2em;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

/* Header stuff starts here */

header {
    width: 70%;
    margin: 0 auto;
}

header h1 {
    text-align: center;
    padding-top: 1.5em;
    font-size: 3em;
}

header h2 {
    text-align: center;
    font-size: 1.35em;
}

nav {
    margin: 0 auto;
    max-width: 70%;
}

header a {
    color: inherit;
    text-decoration: none;
}

header a:hover {
    color: darkslategrey;
}

nav ul {
    list-style-type: none;
    padding-left: 0;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    font-size: 1.4em;
    letter-spacing: 0.09em;
}

nav ul li {
    margin: 1em 1em 2em 1em;
    white-space: nowrap;
}

.blockquoteauthorinfo {
    text-align: end;
}

blockquote {
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 1.2em;
}

/* Main stuff starts here */
main p {
    font-size: 1.15em;
}

main section {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-content: center;
    max-width: 90rem;
    margin: 0 auto;
}

main section .subsection {
    flex: 1 1 auto;
    padding: 0rem 1.5rem;
    width: 40%;
}

main section .subsection h1 {
    text-align: center;
    color: #493A31;
}

main figure>* {
    margin: 0 auto;
    display: block;
    max-width: 100%;
}

#gluttony {
    width: auto;
    height: auto;
    padding-top: 2em;
}

main figcaption {
    padding-top: 0.4em;
}

main figcaption .subcaption {
    padding-top: 0.5em;
}

main figcaption {
    margin: 0 auto;
    width: 80%;
    font-size: 0.8em;
    text-align: center;
    line-height: 1.2em;
    padding-bottom: 2em;
}

/* Footer stuff starts here */

footer {
    background-color: #DDD8CF;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    text-align: center;
}

footer div {
    color: #4D243D;
}

footer .author {
    padding-top: 3em;
}

footer .copyright {
    padding-bottom: 3em;
}

footer div a {
    text-decoration: none;
}

footer a:hover {
    color: #6F3458;
    text-decoration: underline;
}

a:visited {
    color: initial;
}